<template>
  <!-- 首页第四屏 -->
  <div class="home-four bg page-content" id="home-four">
    <div class="main-box">
      <!-- 文字展示部分 -->
      <div class="content-box">
        <!-- 标题 -->
        <p class="title">
          最新案例
          <span class="title-english">Latest Case</span>
        </p>
        <!-- 随走马灯切换而更换的文字 -->
        <div class="carousel-text">
          <p class="content">
            <span> Industry </span>
            <span>{{ content.industry }}</span>
          </p>
          <p class="content">
            <span> Type </span>
            <span>{{ content.type }}</span>
          </p>
          <p class="content">
            <span> Name </span>
            <span>{{ content.name }}</span>
          </p>
        </div>
      </div>
      <!-- 走马灯部分 -->
      <div class="carousel-box">
        <el-carousel :interval="3000" @change="handleCarouselChange" arrow="never" height="26rem"
                     indicator-position="outside">
          <el-carousel-item :key="index" v-for="(item,index) in carouselData">
            <img :src="item.imgUrl" @click="goCase(index)" alt="" class="pointer">
          </el-carousel-item>
        </el-carousel>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        carouselData: [
          {
            imgUrl: require('@/assets/images/home/fourth1.png'),
            industry: '商业街',
            type: '招牌设计',
            name: '翡翠城'
          },
          {
            imgUrl: require('@/assets/images/home/fourth2.png'),
            industry: '写字楼',
            type: '导视设计',
            name: '万银国际'
          },
          {
            imgUrl: require('@/assets/images/home/fourth3.png'),
            industry: '房地产',
            type: '标识设计',
            name: '武林壹号'
          }
        ],
        content: [],
        current: ''
      }
    },
    methods: {
      handleCarouselChange(newIndex, oldIndex) {
        this.content = this.carouselData[newIndex]
        this.current = newIndex + 1
      },
      goCase(index) {
        this.$router.push({path: '/case', query: {caseIndex: index}})
      }
    },
    computed: {
      pageState() {
        // 每当pageState变化时都会调用
        return this.$store.state.pageState
      }
    }
  }
</script>


<style lang="less">
  .home-four {
    .main-box {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
      width: 80%;
      margin: 0 auto;
      .content-box {
        .title {
          font-size: 2.2rem;
          font-weight: 500;

          .title-english {
            display: block;
            font-size: 2.5rem;
            font-weight: 400;
            margin-top: -.3rem;
          }
        }

        .content {
          margin-top: 2.2rem;

          span {
            display: block;
            color: rgba(51, 51, 51, 1);

            &:first-child {
              font-size: 2rem;
            }

            &:last-child {
              font-size: 1.4rem;
            }
          }
        }


        #first-content {
          margin-top: 4rem;
        }
      }

      // carousel需要放到一个父容器中给他定义宽度
      .carousel-box {
        width: 64%;
        text-align: center;
        /*分页器默认状态*/

        .el-carousel__indicator--horizontal {
          padding: 1.2rem 1rem;
          /*分页器按钮*/

          .el-carousel__button {
            width: 2.5rem;
            height: 4px;
            border-radius: 2px;
            background-color: rgba(100, 100, 100, 1);
          }
        }

        /*分页器选中状态*/

        .is-active {
          /*分页器按钮*/

          .el-carousel__button {
            width: 4rem;
            height: 4px;
            border-radius: 2px;
            background-color: rgba(0, 0, 0, 0.8);
          }
        }

        img {
          height: 100%;
        }
      }
    }
  }
</style>
